#wrapper{
    background-color: rgb(226, 226, 226);
    left: 0px;
    right: 0px;
    top: 0px;
    bottom: 0px;
    position: absolute;
}
.thumbnailDivHolder{
    background-color: white;
    width: 90vw;
    height: fit-content;
    display: flex;
    flex-direction: row;
    justify-content: space-around;
    flex-wrap: wrap;
    align-items: center;
    border-radius: 10px;
    box-shadow: grey 20px 20px 20px;
    padding: 10px;
    position: relative;
    margin: 10px auto;
}
.thumbnailDiv {
    width: 200px;
    height: 200px;
    position: relative;
    border-radius: 10px;
    margin: 20px;
    overflow: hidden;
}
.thumbnail{
    width: 100%;
    height: 100%;
    object-fit: cover;
}